import style from  "./diseosUI.module.scss";
import { DiseosData } from "../../data";
import { Swiper, SwiperSlide } from "swiper/react";
import { FreeMode, Navigation, Thumbs } from "swiper";

import "swiper/css";
import "swiper/css/free-mode";
import "swiper/css/navigation";
import "swiper/css/thumbs";

const Diseos = () => {
  return (
    <div className={style.container} id="diseos">
      <h2>Diseos UI/UX</h2>
      <Swiper
        style={{
          "--swiper-navigation-color": "#fff",
          "--swiper-pagination-color": "#fff",
        }}
        loop={true}
        spaceBetween={10}
        navigation={true}
        modules={[FreeMode, Navigation, Thumbs]}
        className={style.swiper1}
      >
        {DiseosData.map((Diseo) => (
          <SwiperSlide className={style.diseosTop} key={Diseo.id}>
            <img src={Diseo.img} alt={Diseo.title} />
          </SwiperSlide>
        ))}
      </Swiper>
    </div>
  );
};

export default Diseos;
